<template>
	<div id="page">
		<h1>PH</h1>
		<iframe ref="gameContainer" id="gameContainer" :src="gameUrl"></iframe>
	</div>
</template>
<script>
	export default {
		name: 'Game',
		computed: {
			gameUrl() {
				return `http://${window.location.host}/game/${this.$store.state.gameName}/index.html`;
			},
		},
		methods: {
			addOrientationListener() {
				console.log('orientation listened');
				if (window.DeviceOrientationEvent) {
					window.addEventListener(
						'deviceorientation',
						(evt) => {
							var iframe =
								document.getElementById('gameContainer').contentWindow;
							iframe.postMessage(
								{
									orientation: window.orientation,
								},
								'*'
							);
							console.log(evt);
						},
						false
					);
				} else {
					alert('ori geht nicht');
				}
			},
		},
		mounted() {
			console.log('test');
			this.$refs.gameContainer.contentWindow.socket = this.$socket;
			this.addOrientationListener();
		},
	};
</script>

<style scoped>
	#page {
		display: flex;
		flex-direction: column;
		height: 95vh;
	}
	#gameContainer {
		flex-grow: 1;
	}
</style>
